    "Lego.box1Paragraph2":
      "Vous devez, √† l‚Äôaide de la carte, r√©aliser un parcours d‚Äôorientation classique, et retrouver les bonnes balises sur le terrain LEGO. Ceci dans le bon ordre et le plus vite possible.",
    "Lego.box1Paragraph3": "Un exemple de terrain en LEGO avec des balises :",
    "Lego.box1Paragraph4": "Et la carte qui correspond:",
    "Lego.box1Paragraph5":
      "Vous pointez le d√©part avec votre propre puce (ou une puce pr√™t√©e). Ensuite, il vous faut choisir les bonnes balises sur le terrain √† l‚Äôaide de la carte. Pour cela, il est possible de tourner autour de la table LEGO et sur chaque balise, un num√©ro de poste est √† reporter sur le carton de contr√¥le √† l‚Äôaide d‚Äôun stylo (votre propre stylo ou un stylo pr√™t√©). Vous pointez l‚Äôarriv√©e pour finir la course. Les HD10 devront retrouver des animaux plut√¥t que des balises.",
    "Lego.box1Paragraph61":
      "Vous pouvez retrouver toutes ses informations sur ",
