    "Accomodation.title": "H√©bergements",
    "Accomodation.box1Title": "Locations",
    "Accomodation.box11Paragraph":
      "Il y a de nombreuses possibilit√©s de location d‚Äôh√©bergement de type g√Æte aux alentours de Fontainebleau et dans le sud de la Seine et Marne.",
    "Accomodation.box12Paragraph":
      "La region est pris√©e des grimpeurs et randonneurs, il est donc conseill√© de r√©server un h√©bergement le plus t√¥t possible pour √™tre s√ªr d‚Äôavoir de la place.",
    "Accomodation.box13Paragraph":
      "Voici quelques sites web recensant des h√©bergements disponibles dans la region:",
    "Accomodation.box131Paragraph": " (Pr√®s de Nemours)",
    "Accomodation.box14Paragraph":
      "Voici quelques sites web g√©n√©raux pour une recherche plus vaste:",
    "Accomodation.box2Title": "Campings",
    "Accomodation.box2Paragraph":
      "Il y a plusieurs campings autour de la for√™t de fontainebleau. Voici une liste non exhaustive des campings les plus populaires:",
    "Accomodation.box21Paragraph": " (pr√®s de Montigny-sur-loing)",
    "Accomodation.box22Paragraph": " (pr√®s de Noisy-sur-√âcole)",
    "Accomodation.box23Paragraph": " (pr√®s de Malesherbes)",
    "Accomodation.box24Paragraph": " (pr√®s de Veneux-les-Sablons)",
    "Accomodation.box3Title": "Office de tourisme",
    "Accomodation.paragraph":
      "Vous pouvez aussi consulter les sites des offices de tourisme de Fontainebleau, Melun et Nemours pour plus de renseignements sur les h√©bergements dans la r√©gion",
    "Accomodation.officeFontaineblau": "Office de tourisme de Fontainebleau",
    "Accomodation.officeMelun": "Office de tourisme de Melun",
    "Accomodation.officeNemours": "Office de tourisme de Nemours",
    "Accomodation.officeGatinais":
      "Office de tourisme du G√¢tinais Val-de-Loing",
    "Home.title": "Accueil",
    "Home.bacgroundParagraph":
      "3 jours de course d'orientation en for√™t de Fontainebleau et ses environs",
    "Home.baliseSpTitle": "Sprint",
    "Home.baliseSpParagraph1": "21",
    "Home.baliseSpParagraph2": "ao√ªt",
    "Home.baliseMdTitle": "MD",
    "Home.baliseMdParagraph1": "22",
    "Home.baliseMdParagraph2": "ao√ªt",
    "Home.baliseLdTitle": "LD",
    "Home.baliseLdParagraph1": "23",
    "Home.baliseLdParagraph2": "ao√ªt",
    "Home.box1Title": "Informations",
    "Home.box11Paragraph":
      "Les Nationales Nord Ouest 2020 de course d‚Äôorientation auront lieu du 21 au 23 ao√ªt 2020 dans la for√™t de Fontainebleau. Ce long week-end regroupe trois courses comptant pour le classement national:",
    "Home.box121Paragraph":
      "Un Sprint Urbain le vendredi 21 ao√ªt apr√®s midi dans la ville de Ch√¢teau-Landon",
    "Home.box122Paragraph":
      "Une Moyenne Distance le samedi 22 ao√ªt apr√®s midi sur la carte de la Salamandre",
    "Home.box123Paragraph":
      "Une Longue Distance le dimache 23 ao√ªt matin sur la carte de Franchard",
    "Home.box13Paragraph":
      "Le terrain du sprint urbain est un petit village typique du sud de la Seine et Marne avec quelques petites ruelles ainsi que des quartiers r√©sidentiels offrant une vitesse de course plus √©lev√©e. La carte poss√®de √©galement plusieurs parcs ou pr√©s qui n√©cessiteront aux coureurs de s‚Äôadapter aux diff√©rents environnements.",
    "Home.box14Paragraph":
      "Les terrains de la moyenne distance et de la longue distance sont similaires et typiques des terrains de la for√™t de fontainebleau. La carte poss√®de de nombreux chaos rocheux le plus souvent sur les flancs et des nombreuses collines sableuses qui rendent l‚Äôorientation difficile. Entre ces zones d√©taill√©es, la visibilit√© est bien meilleure et un r√©seau de chemin assez dense devrait permettre une navigation plus ais√©e.",
    "Home.box15ParagraphTitle": "Garderie: ",
    "Home.box15Paragraph":
      "Face au COVID-19, nous ne serons pas en mesure de mettre en place une garderie sur ces 3 jours, Merci de prendre vos dispositions. Il est possible en dernier recours pour les parents, de demander lors des inscriptions des heures de d√©part d√©cal√©es, nous ferons notre possible pour respecter vos souhaits compte tenu que nous devons aussi tenir compte des plages horaires par cat√©gories.",
